'use client';

import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  Grid,
  IconButton,
  Menu,
  MenuItem,
  Fade,
  Button
} from '@mui/material';
import {
  AccountCircle,
  Notifications,
  ExitToApp,
  People
} from '@mui/icons-material';
import UserManagement from '../admin/UserManagement';

interface DashboardProps {
  onLogout: () => void;
}

const Dashboard: React.FC<DashboardProps> = ({ onLogout }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [currentView, setCurrentView] = useState<'admin'>('admin');

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    handleClose();
    onLogout();
  };

  return (
    <Box sx={{ minHeight: '100vh', bgcolor: '#f1f5f9', display: 'flex', flexDirection: 'column' }}>

      {/* Top Accessibility Bar */}
      <Box sx={{ bgcolor: '#1e293b', color: '#cbd5e1', py: 0.5, px: 3, display: 'flex', justifyContent: 'flex-end', borderBottom: '1px solid #334155' }}>
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
          <Typography variant="caption" sx={{ fontSize: '11px', mr: 2 }}>Skip to Main Content</Typography>
          {['A', 'A', '-', 'A', '+', 'En'].map((text, index) => (
            <Box
              key={index}
              component="button"
              sx={{
                border: '1px solid #475569',
                bgcolor: 'transparent',
                color: 'inherit',
                px: 1,
                fontSize: '10px',
                borderRadius: 0.5,
                cursor: 'pointer',
                '&:hover': { bgcolor: '#334155', color: 'white' },
                transition: 'all 0.2s'
              }}
            >
              {text}
            </Box>
          ))}
        </Box>
      </Box>

      {/* Main Header */}
      <Box sx={{ bgcolor: 'white', py: 2, borderBottom: '1px solid #e2e8f0', boxShadow: '0 4px 6px -1px rgba(0,0,0,0.05)', position: 'relative', zIndex: 10 }}>
        <Container maxWidth="xl">
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2.5 }}>
              <Box
                sx={{
                  width: 80,
                  height: 80,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  p: 0.5,
                  mr: 1
                }}
              >
                <img src="/images/karnataka_emblem.png" alt="Karnataka Government Logo" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
              </Box>
              <Box>
                <Typography variant="h5" sx={{ fontWeight: 800, color: '#0f172a', lineHeight: 1.2, letterSpacing: '-0.5px' }}>
                  Directorate of Municipal Administration
                </Typography>
                <Typography variant="subtitle2" sx={{ color: '#002147', fontWeight: 700, letterSpacing: '1.5px', textTransform: 'uppercase', mt: 0.5 }}>
                  Government of Karnataka
                </Typography>
              </Box>
            </Box>

            <Box sx={{ display: { xs: 'none', md: 'block' } }}>
              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
                <Typography variant="caption" sx={{ color: '#64748b', mb: 0.5 }}>Helpline Number</Typography>
                <Typography variant="body2" sx={{ fontWeight: 700, color: '#002147' }}>1800-425-1111</Typography>
              </Box>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Navigation Bar */}
      <Box sx={{
        bgcolor: '#002147',
        py: 1,
        px: 3,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        background: 'linear-gradient(to right, #002147, #0f172a)'
      }}>
        <Container maxWidth="xl" sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Button
              startIcon={<People />}
              onClick={() => setCurrentView('admin')}
              sx={{
                color: currentView === 'admin' ? 'white' : '#94a3b8',
                bgcolor: currentView === 'admin' ? 'rgba(255,255,255,0.1)' : 'transparent',
                '&:hover': { bgcolor: 'rgba(255,255,255,0.2)', color: 'white' },
                fontWeight: 700
              }}
            >
              Admin
            </Button>
          </Box>

          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', color: 'white' }}>
            <IconButton size="small" sx={{ color: 'white', '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' } }}>
              <Notifications fontSize="small" />
            </IconButton>
            <Box>
              <IconButton
                size="small"
                onClick={handleMenu}
                sx={{
                  color: 'white',
                  '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
                  border: '1px solid rgba(255,255,255,0.2)'
                }}
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                PaperProps={{
                  elevation: 0,
                  sx: {
                    overflow: 'visible',
                    filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                    mt: 1.5,
                    '& .MuiAvatar-root': {
                      width: 32,
                      height: 32,
                      ml: -0.5,
                      mr: 1,
                    },
                    '&:before': {
                      content: '""',
                      display: 'block',
                      position: 'absolute',
                      top: 0,
                      right: 14,
                      width: 10,
                      height: 10,
                      bgcolor: 'background.paper',
                      transform: 'translateY(-50%) rotate(45deg)',
                      zIndex: 0,
                    },
                  },
                }}
              >
                <MenuItem onClick={handleClose}>Profile</MenuItem>
                <MenuItem onClick={handleLogout} sx={{ color: 'error.main' }}>
                  <ExitToApp sx={{ mr: 1 }} />
                  Logout
                </MenuItem>
              </Menu>
            </Box>
          </Box>
        </Container>
      </Box>

      {/* Main Content */}
      <Container maxWidth="xl" sx={{ mt: 4, mb: 4, flexGrow: 1 }}>
        <Fade in timeout={800} key={currentView}>
          <Box>
            <UserManagement />
          </Box>
        </Fade>
      </Container>
    </Box>
  );
};

export default Dashboard;